
import React, { useContext } from 'react';
import { NavLink, useHistory } from "react-router-dom";
import context from './Context';
import { isMobile } from 'react-device-detect';
import logo from '../assets/logo.svg';
const App = (props) => {
  const state = useContext(context);
  const history = useHistory();
  const { user, setUser, setYear } = state;
  const logOut = () => {
    setUser(null);
    setYear(null);
    // localStorage   
    localStorage.removeItem('user');
    localStorage.removeItem('year');

    history.replace('/');
  }

  const change = () => {
    history.push('/change');
  }

  /*useEffect(() => {
    const storedUser = localStorage.getItem('user');
    if (!storedUser) {
      history.replace('/');
    } else {
      history.push('/');
    }
  }, [history, setUser, setYear]);*/

  return (
    <header className="head">
      <nav className='nav sub'>
        <div className='headGroup'>
          <div className='headTitle'><img src={logo} alt='MND' /><span></span></div>
          {
            isMobile &&
            <>
              <button className='change' onClick={change} title=""><i className="ri-user-settings-line"></i></button>
              <button className='logout' onClick={logOut} title=""><i className="ri-logout-box-r-line"></i></button>
            </>
          }
        </div>
        <div className='navRes'>
          <NavLink className='navButton' exact to="/" title=""><i className="ri-survey-line"></i><span></span></NavLink>
          <NavLink className='navButton' exact to="/write" title=""><i className="ri-pencil-line"></i><span></span></NavLink>
        </div>
        {
          !isMobile &&
          <div className='headRight'>
            <span>{user && " "+user+" "}</span>
            <button className='change' onClick={change} title=""><i className="ri-user-settings-line"></i></button>
            <button className='logout' onClick={logOut} title=""><i className="ri-logout-box-r-line"></i></button>
          </div>
        }
      </nav>
    </header>
  );
}
export default App;